<template>
  <div class="kingkong">
    <ul>
      <li v-b-modal.modalTols>
        <button type="button" class="btn btn-secondary">
          <b-icon icon="tools" aria-hidden="true"></b-icon>
        </button>
        <span>攜帶工具</span>
      </li>
      <li @click="clearLocalStorage">
        <button type="button" class="btn btn-secondary">
          <b-icon icon="bootstrap-reboot" aria-hidden="true"></b-icon>
        </button>
        <span>重置資料</span>
      </li>
    </ul>
  </div>
</template>

<style lang="sass" scoped src="./kingkong.sass"></style>

<script>
export default {
  methods: {
    clearLocalStorage() {
      let result = confirm('確定要重置資料?')
      if (!result) return
      localStorage.clear()
      location.reload()
    },
  },
}
</script>
