<template lang="pug">
  .list__tags
    span(
      v-for="item in value"
      :key="item.id"
      :class="['badge', 'badge-pill', 'badge-dark', { active: item.active }]"
      @click="command(item)"
    ) {{ item.title }}
</template>

<script>
export default {
  props: {
    /** 值 */
    value: {
      type: Array,
      default: () => [],
    },
  },
  watch: {
    value: {
      handler(newValue) {
        this.$emit('update:value', newValue)
        return
      },
      immediate: true,
    },
  },
  methods: {
    command(item) {
      this.value.forEach(x => {
        x.active = false
        if (item.id == x.id) {
          x.active = true
        }
      })
      /**
       * 更新事件
       * @property {Number} value
       */
      this.$emit('update:value', this.value)
    },
  },
}
</script>

<style lang="sass" scoped>
.list__tags
  max-width: 150px
  display: flex
  align-items: center
  overflow-y: hidden
  overflow-x: auto
  -webkit-overflow-scrolling: touch
.badge
  background-color: #333
  .theme-default &
    background-color: #000
  & + .badge
    margin-left: 5px
  &:not(.active)
    background-color: rgba(#fff, 0.1)
</style>
