<template>
  <div class="counter card bg-primary text-white text-left">
    <div class="counter__left">
      <span class="title">檢驗數量與合格量</span>
      <span class="num"
        >{{ getCountData.totalLen }} / {{ getCountData.completeLen }}</span
      >
    </div>
    <div class="counter__right">
      <span class="title">合格率</span>
      <span class="num">{{ successPercent }}%</span>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    getCountData() {
      return this.$store.getters['db/getCountData']
    },
    successPercent() {
      let { totalLen, completeLen } = this.getCountData
      let res = Math.floor((completeLen / totalLen) * 100)
      return res
    },
  },
}
</script>
