let state = function () {
  return {
    db: {},
  }
}
export default {
  namespaced: true,
  state,
  getters: {
    getCountData(state) {
      let _db = state.db
      let countAll = 0
      let countComplete = 0
      Object.keys(_db).forEach(storageName => {
        let list = _db[storageName]['cardCheckList']
        Object.keys(list).forEach(key => {
          countAll = countAll + list[key].length
          let _allActive = list[key].filter(item => {
            return item.active == true
          })
          countComplete = countComplete + _allActive.length
        })
      })
      return {
        totalLen: countAll,
        completeLen: countComplete,
      }
    },
  },
  mutations: {
    updateDB(state, payload) {
      let { storageName, data } = payload
      let singleData = {}
      singleData[storageName] = data
      Object.keys(state.db).forEach(key => {
        singleData[key] = state.db[key]
      })
      state.db = singleData
    },
  },
  actions: {},
}
