export default {
  props: {
    // storage name 必需唯一
    storageName: {
      type: String,
    },
    // 卡片標題
    title: {
      type: String,
      default: '',
    },
    // init data
    cardTabs: {
      type: Array,
    },
    // init data
    cardCheckList: {
      type: Object,
    },
  },
  data() {
    return {
      modalPrive: {
        title: 'Test title',
        note: 'test note',
      },
      tabs: [],
      checkList: {},
    }
  },
  methods: {
    showModal(item) {
      this.modalPrive = item
      this.$refs['modalPopover'].show()
    },
  },
  watch: {
    db: {
      handler(newValue) {
        localStorage.setItem(this.storageName, JSON.stringify(newValue))
        this.$store.commit('db/updateDB', {
          storageName: this.storageName,
          data: newValue,
        })
      },
      deep: true,
    },
  },
  computed: {
    completed() {
      let res = ''
      let totalLen = 0
      let completedLen = 0
      let _checkList = this.checkList
      Object.keys(_checkList).forEach(key => {
        totalLen = totalLen + _checkList[key].length
        completedLen =
          completedLen + _checkList[key].filter(x => x.active == true).length
      })
      if (totalLen == completedLen) {
        return 'done'
      }
      res = `${totalLen}/${completedLen} 完成度`
      return res
    },
    db() {
      return { cardTabs: this.tabs, cardCheckList: this.checkList }
    },
  },
  mounted() {
    var _localStorage = localStorage.getItem(this.storageName)
    if (_localStorage == null || _localStorage == 'undefined') {
      this.tabs = this.cardTabs
      this.checkList = this.cardCheckList
    } else {
      let db = JSON.parse(_localStorage)
      this.tabs = db.cardTabs
      this.checkList = db.cardCheckList
    }
  },
}
